import React from "react";
import background from "../asesst/3658600.jpg";
import Top from "../topside/Top";
import Middel from "../middel/Middel";
import { useFormik } from "formik";
import * as Yup from "yup";
import { useDispatch } from "react-redux";
import { getsearch } from "../sharedata/Search";
import { useNavigate } from "react-router-dom";
export default function Home() {
  let navg = useNavigate();
  let disp: any = useDispatch();
  let valid = Yup.object({
    search: Yup.string()
      .max(30, "max Char 30")
      .min(1, "min Char 1")
      .required("search Required"),
  });

  function form(valu: any) {
    
    disp(getsearch(valu.search));
    navg("/search");
  }
  let formk = useFormik({
    initialValues: {
      search: "",
    },
    onSubmit: form,
    validationSchema: valid,
  });
  return (
    <div>
      <div className="position-relative ">
        <img src={background} alt="" className="w-100"></img>
        <div className="">
          <div className="position-absolute top-0 end-0 start-0 bottom-0  opacity-75  ">
            <div className="d-flex justify-content-center h-100 align-items-center ">
              <form onSubmit={formk.handleSubmit} className="w-100 text-center">
                <input
                  className="form-control-sm rounded-5 w-50 fw-bolder fs-5 text-success"
                  type="text"
                  name="search"
                  onChange={formk.handleChange}
                  onBlur={formk.handleBlur}
                ></input>
                <button className=" btn-success btn rounded-5 fw-bolder fs-5">
                  search
                </button>
              </form>
            </div>
          </div>
        </div>
      </div>
      <Top></Top>
      <></>
      <Middel></Middel>
    </div>
  );
}
