import React from "react";
import { useSelector } from "react-redux";

import { Searchinterface } from "../interface/searchinterface";


export default function Search() {
  let { loading, searchresult } = useSelector(
    (state: any) => state.SearchResultDate
  );

  let list: Searchinterface = searchresult;

  return (
    <div>
      {loading === false ? (
        <div className="position-fixed top-0 end-0 start-0 bottom-0 bg-gradient d-flex justify-content-center bg-white align-items-center z-2">
          <div id="container">
            <div className="d-flex">
              <h2 className="fw-bolder mx-5 z-3">loading</h2>
              <span className="loader"></span>
            </div>
          </div>
        </div>
      ) : (
        <>
          {list?.results?.map((el) => {
            return (
              <>
                <div className=" my-3 px-5 border-bottom border-2" key={el.id}>
                  <div className="row p-3">
                    <div className="col-md-3">
                      <img
                        src={
                          "https://image.tmdb.org/t/p/w500/" + el?.backdrop_path
                        }
                        alt={el?.title}
                        className="w-100 rounded-4 "
                      ></img>
                    </div>
                    <div className="col-md-9">
                      <h1>
                        <span>{el?.original_title}</span>(
                        <>
                          {" "}
                          {el?.release_date?.toString().split("-").slice(0, 1)}
                        </>
                        )
                      </h1>

                      <h2>
                        <span className="text-capitalize">
                          overview
                          <span className="px-3">
                            {el?.vote_average?.toFixed(1)}
                            <i className="fa-solid fa-star text-warning"></i>
                          </span>
                        </span>
                        <p className="text-muted">
                          {el?.overview.split(" ").slice(0, 20).join(" ")}
                        </p>
                      </h2>
                    </div>
                  </div>
                </div>
              </>
            );
          })}
        </>
      )}
    </div>
  );
}
