import { createAsyncThunk, createSlice } from "@reduxjs/toolkit";
import axios from "axios";
export let getsearch = createAsyncThunk(
  "slice/search",
  async function (result: any) {
    const options = {
      method: "GET",
      headers: {
        accept: "application/json",
        Authorization:
          "Bearer eyJhbGciOiJIUzI1NiJ9.eyJhdWQiOiIzMTgwZGZhYjY4ZTc0MWNjMTU1YTUyMTIzMDMwNDQ3MCIsInN1YiI6IjY1Yzc1ZmM2YWFkOWMyMDE3ZGI2YTIzMCIsInNjb3BlcyI6WyJhcGlfcmVhZCJdLCJ2ZXJzaW9uIjoxfQ.nKGepfyE69ZVsHAtH5ikWCAIxnervV0xa-cFf7Ykbg0",
      },
    };

    let req = await axios.get(
      `https://api.themoviedb.org/3/search/movie?query=${result}&include_adult=false&language=en-US&page=1`,
      options
    );
    // console.log(req.data);
    return req.data;
  }
);

let searchslice = createSlice({
  name: "search",
  initialState: { searchresult: [], loading: false },
  reducers: {},
  extraReducers: function (builder) {
    builder.addCase(getsearch.pending, function (state, action) {
      state.loading = false;
    });
    builder.addCase(getsearch.fulfilled, function (state, action) {
      state.searchresult = action.payload;
      state.loading = true;
    });
    builder.addCase(getsearch.rejected, function (state, action) {});
  },
});

export default searchslice.reducer;
