import { createAsyncThunk, createSlice } from "@reduxjs/toolkit";
import axios from "axios";

export let getdetails = createAsyncThunk(
  "detail/slice",
  async function (id: any) {
    const options = {
      method: "GET",
      headers: {
        accept: "application/json",
        Authorization:
          "Bearer eyJhbGciOiJIUzI1NiJ9.eyJhdWQiOiIzMTgwZGZhYjY4ZTc0MWNjMTU1YTUyMTIzMDMwNDQ3MCIsInN1YiI6IjY1Yzc1ZmM2YWFkOWMyMDE3ZGI2YTIzMCIsInNjb3BlcyI6WyJhcGlfcmVhZCJdLCJ2ZXJzaW9uIjoxfQ.nKGepfyE69ZVsHAtH5ikWCAIxnervV0xa-cFf7Ykbg0",
      },
    };

    let req = await axios.get(
      `https://api.themoviedb.org/3/movie/${id}?language=en-US`,
      options
    );

    return req.data;
  }
);

let Details = createSlice({
  name: "Details",
  initialState: { movidetails: [], loading: false },
  reducers: {},
  extraReducers: function (builder) {
    builder.addCase(getdetails.pending, function (state, action) {
      state.loading = false;
    });
    builder.addCase(getdetails.fulfilled, function (state, action) {
      state.movidetails = action.payload;
      state.loading = true;
    });
    builder.addCase(getdetails.rejected, function (state, action) {});
  },
});
export default Details.reducer;
