import axios from "axios";
import React, { useEffect, useState } from "react";
import OwlCarousel from "react-owl-carousel";
import "owl.carousel/dist/assets/owl.carousel.css";
import "owl.carousel/dist/assets/owl.theme.default.css";
import $ from "jquery";
import { Link } from "react-router-dom";
import { Middelinterface, Result } from "../interface/middel";

export default function Middel() {
  let [movieslist, sendllist] = useState<Result[]>([]);
  let [counter, sendcount] = useState<number>(1);
  let [spin, sendspin] = useState<boolean>(true);
  useEffect(() => {
    getall("all");
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  async function getall(valu = "all") {
    const options = {
      method: "GET",
      headers: {
        accept: "application/json",
        Authorization:
          "Bearer eyJhbGciOiJIUzI1NiJ9.eyJhdWQiOiIzMTgwZGZhYjY4ZTc0MWNjMTU1YTUyMTIzMDMwNDQ3MCIsInN1YiI6IjY1Yzc1ZmM2YWFkOWMyMDE3ZGI2YTIzMCIsInNjb3BlcyI6WyJhcGlfcmVhZCJdLCJ2ZXJzaW9uIjoxfQ.nKGepfyE69ZVsHAtH5ikWCAIxnervV0xa-cFf7Ykbg0",
      },
    };
    sendspin(true);

    let req = await axios
      .get(
        `https://api.themoviedb.org/3/trending/${valu}/week?language=en-US&page=${counter}`,
        options
      )
      .catch((eerr) => {
        // console.log(eerr);
      });
    let data: Middelinterface = req?.data;
    sendllist(data.results);
    sendspin(false);
    // console.log(data);
    // console.log(data.results);
  }

  function active(event: any) {
    let valu = event.target.getAttribute("typeof");
    getall(valu);
  }

  $(".nav-link").on("click", function (e) {
    $(e.target).addClass(" bg-black opacity-50 rounded-5 text-info");
    $(e.target)
      .siblings()
      .removeClass(" bg-black opacity-50 roundedd-5 text-info");
  });

  const optain = {
    loop: true,
    margin: 10,
    responsiveClass: true,
    responsive: {
      0: {
        items: 1,
      },
      600: {
        items: 3,
      },
      1000: {
        items: 7,
      },
    },
  };

  function next() {
    let page = counter + 1;
    if (counter < 100) {
      sendcount(page);
      getall();
    } else {
      sendcount(100);
      getall();
    }
  }
  function Previous() {
    let page = counter - 1;
    if (counter > 1) {
      sendcount(page);
      getall();
    } else {
      sendcount(1);
      getall();
    }
  }

  return (
    <div>
      <div className=" ground pb-3">
        <div className=" smallnav  px-4 py-1 mx-4 rounded-5 d-flex justify-content-center opacity-75 pt-4">
          <nav className="nav">
            <span className="fw-bolder fs-4 text-dark pe-1">TRENDING</span>
            <p
              className="nav-link  fw-bolder text-uppercase text-success"
              onClick={active}
              typeof="all"
            >
              ALL
            </p>
            <p
              className="nav-link fw-bolder text-uppercase text-success"
              onClick={active}
              typeof="tv"
            >
              tv
            </p>
          </nav>
        </div>
        {spin === true ? (
          <div className="position-fixed top-0 end-0 start-0 bottom-0 bg-gradient d-flex justify-content-center bg-white align-items-center z-2">
            <div id="container">
              <div className="d-flex">
                <h2 className="fw-bolder mx-5 z-3">loading</h2>
                <span className="loader"></span>
              </div>
            </div>
          </div>
        ) : (
          <>
            <OwlCarousel className="owl-carousel owl-theme mt-3  " {...optain}>
              {movieslist.map((el) => {
                return (
                  <>
                    {el.id < 9999999999999 && el.media_type === "movie" ? (
                      <Link
                        className="nav-link"
                        to={`Details/${el?.id}`}
                        key={el.id}
                      >
                        <div className="item " key={el.id}>
                          <img
                            src={
                              "https://image.tmdb.org/t/p/w500/" +
                              el.poster_path
                            }
                            alt={el.title}
                            className="w-100 rounded-5 "
                          ></img>
                          <h6 className="px-3">
                            {el.title?.split(" ").slice(0, 5).join(" ")}
                          </h6>
                          <span className="px-3">
                            {el.vote_average?.toFixed(1)}
                            <i className="fa-solid fa-star text-warning"></i>
                          </span>
                          <h6 className="px-3 text-muted">
                            <>{el.release_date}</>
                          </h6>
                        </div>
                      </Link>
                    ) : (
                      ""
                    )}
                  </>
                );
              })}
            </OwlCarousel>
            <div className=" d-flex justify-content-center">
              <button
                className="btn btn-sm btn-success mx-1"
                onClick={Previous}
              >
                Previous
              </button>
              <button className="btn btn-sm btn-dark mx-1">{counter}</button>
              <button className="btn btn-sm btn-success mx-1" onClick={next}>
                next
              </button>
            </div>
          </>
        )}
      </div>
    </div>
  );
}
